:root {
  --font-color: #fff;
  --white: #fff;
  --black: #000;
  --gray: #ced4da;
  --blue-background: #74c0fc;
  --dark-blue: #364fc7;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: var(--font-color);
  background-color: var(--blue-background);
  font-family: Rubik, sans-serif;
  font-weight: 400;
  line-height: 1;
}

.main {
  max-width: 45rem;
  margin: 0 auto;
  padding: 3.2rem;
}

.grid--2 {
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.grid--3 {
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  display: grid;
}

.grid--7 {
  grid-template-columns: repeat(7, 1fr);
  align-items: center;
  display: grid;
}

.flex--v {
  text-align: center;
  flex-direction: column;
  gap: 1.6rem;
  margin: 0 auto;
  font-size: 2rem;
  display: flex;
}

.line--v {
  background-color: var(--white);
  width: 2px;
  height: 80%;
  margin: .5rem auto;
}

.line--h {
  background-color: var(--white);
  width: auto;
  height: 2px;
  margin: 2rem auto;
}

.parent__description {
  text-align: center;
  font-size: 2rem;
  line-height: 1.3;
  display: block;
}

.spinner {
  height: 8rem;
  margin: 4.8rem auto;
  animation: 2.5s linear infinite rotate;
  display: block;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.search {
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
  display: flex;
}

.search__input {
  border: none;
  border-radius: 99999rem;
  width: 85%;
  padding: 1rem 2rem;
  font-family: inherit;
}

.search__submit--image {
  height: 2.4rem;
  margin: 1rem;
}

.search__submit {
  border: none;
  border-radius: 99999rem;
  width: 4.8rem;
  height: 4.8rem;
}

.search__submit:hover {
  background-color: var(--gray);
}

.info {
  padding: 1rem;
}

.degree {
  width: 15rem;
  font-size: 12.8rem;
  display: block;
}

.degree--unit {
  font-size: 3.6rem;
  display: block;
  transform: translate(15rem, -5rem);
}

.condition {
  flex-direction: column;
  align-items: center;
  margin-left: 8rem;
  display: flex;
}

.condition__description {
  font-size: 3.6rem;
}

.condition__image {
  width: 10rem;
  height: 10rem;
}

.city__name {
  font-size: 3.6rem;
}

.city__time {
  justify-self: end;
  font-size: 3.6rem;
}

@media (width <= 25em) {
  html {
    font-size: 50%;
  }
}

@media (width <= 20em) {
  html {
    font-size: 43.75%;
  }
}
